<template>
  <div id="notebook-list">
    <header>
      <a href="#" class="btn" @click.prevent="onCreate"><i class="iconfont icon-plus"></i>新建笔记本</a>
    </header>
    <main>
      <div class="layout">
        <h3>笔记本列表（{{notebooks.length}}）</h3>
        <div class="book-list">
          <router-link class="notebook" v-for="notebook in notebooks" :to="`/note?notebookId=${notebook.id}`">
            <div>
              <span class="iconfont icon-notebook"></span>{{notebook.title}}
              <span>{{notebook.noteCounts}}</span><span class="action" @click.stop.prevent="onEdit(notebook)">编辑</span>
              <span class="action" @click.stop.prevent="onDelete(notebook)">删除</span>
              <span class="date">{{ notebook.createdAtFriendly }}</span>
            </div>
          </router-link>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import {friendlyDate} from '@/helpers/util'
import {mapState, mapGetters, mapActions} from 'vuex'

export default {
  name: "NotebookList",
  data(){
    return {}
  },
  created() {
    // 如果没有登录，点击笔记本，自动跳转到登录页面
    this.checkLogin({ path: '/login'})
    console.log('登录成功')
    this.getNotebooks()
  },

  computed: {
    ...mapGetters([
      'notebooks',
    ])
  },
  methods: {
    ...mapActions([
      'getNotebooks',
      'addNotebook',
      'updateNotebook',
      'deleteNotebook',
      'checkLogin'
    ]),
    onCreate() {
      this.$prompt('输入笔记本名称', '创建笔记本', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /^.{1,30}$/,
        inputErrorMessage: '标题不能为空，且不超过30个字符'
      }).then(({ value }) => {
        this.addNotebook({ title: value })
      })
    },

    onEdit(notebook){
      this.$prompt('输入笔记本名称', '修改笔记本', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /^.{1,30}$/,
        inputValue: notebook.title,
        inputErrorMessage: '标题不能为空，且不超过30个字符'
      }).then(({ value }) => {
        this.updateNotebook({notebookId: notebook.id,  title: value })
      })
    },

    onDelete(notebook){
      this.$confirm('是否删除该笔记本?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteNotebook({notebookId: notebook.id})
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import "../assets/css/notebook-list.less";
</style>



